<template>
  <div class="container">
    <h1>Bookshelf</h1>
    <div class="create-book">
        <label for="create-book">Title</label>
        <input type="text" id="create-book" v-model="text" placeholder="Enter a book title">
        <button v-on:click="createBook">Enter</button>
    </div>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="books-container">
      <div class="book"
        v-for="(book, index) in books"
        v-bind:item="book"
        v-bind:index="index"
        v-bind:key="book._id"
        v-on:dblclick="deleteBook(book._id)">
        {{ `${book.createdAt.getDate()}/${book.createdAt.getMonth()}/${book.createdAt.getFullYear()}` }}
        <p class="text">{{ book.text }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import BookService from '../BookService';

export default {
  name: 'BookComponent',
  data() {
    return {
      books: [],
      error: '',
      text: ''
    }
  },
  async created() {
    try {
      this.books = await BookService.getBooks();
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    async createBook() {
      await BookService.insertBook(this.text);
      this.books = await BookService.getBooks();
      this.text = '';
    },
    async deleteBook(id) {
      await BookService.deleteBook(id);
      this.books = await BookService.getBooks();
    }
  }
}
</script>

